  min-width: 0;
  height: 8px;
  min-height: auto;
  padding: 0;
  border-radius: 50%; }

.badge-up {
  position: absolute;
  top: 25%;
  right: 0;
  min-width: 1rem;
  min-height: 1rem;
  padding: 0 .25rem;
  line-height: 1rem;
  background-clip: padding-box;
  -webkit-transform: translate(0%, -50%);
  transform: translate(0%, -50%); }

